.TimelineObject, .HobbieObject {
    width: 26%;
    margin-right: 10px; margin-bottom: 10px;    
}

#timeline-object-card {
    width: 100%; background-color: white;
    -o-transition: 0.3s;
    transition: 0.3s;
    -webkit-transition: 0.3s; -moz-transition: 0.3s;
}

#timeline-object-card:hover{
    -webkit-box-shadow: 7px 7px 7px silver;
            box-shadow: 7px 7px 7px silver;  
}

#timeline-object-card-flag-image {
    filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale"); /* Firefox 3.5+ */
    filter: gray; /* IE6-9 */
    -webkit-filter: grayscale(100%); /* Chrome 19+ & Safari 6+ */
    -webkit-transition: all .3s ease;
    -webkit-backface-visibility: hidden;
}

#timeline-object-card:hover > #timeline-object-card-flag-image{
    filter: none;
    -webkit-filter: grayscale(0%);
}

#timeline-object-card-title {
    background-color: whitesmoke;
}

#timeline-object-actions {
    display: -ms-flexbox;
    display: flex; -ms-flex-direction: row; flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap; background-color: whitesmoke;
    padding-left: 20px;
}

#timeline-object-actions-text {
    margin-right: 10px;
}

#timeline-object-card-subheader {
    display: -ms-flexbox;
    display: flex; -ms-flex-direction: row; flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}

#timeline-object-card-subheader-year {
    margin-left: auto; margin-right: 0px;
    font-weight: bolder; width: -webkit-max-content; width: -moz-max-content; width: max-content;
}

#timeline-object-card-subheader-country {
    margin-left: 0px; margin-right: auto;
    font-weight: normal; width: -webkit-max-content; width: -moz-max-content; width: max-content;
}


