.SkillObject {
    width: 30%; height: -webkit-max-content; height: -moz-max-content; height: max-content;
    margin-right: 10px; margin-bottom: 10px;
}

#skill-object-card {
    display: -ms-flexbox;
    display: flex; -ms-flex-direction: row; flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}

#skill-object-card-details {
    display: -ms-flexbox;
    display: flex; -ms-flex-direction: column; flex-direction: column;
    width: 70%; padding-top: 5px; padding-bottom: 5px;
}

#skill-object-card-image {
    width: 30%; margin: auto; background-color: whitesmoke;
    padding-top: 2px; padding-bottom: 2px;
    filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale"); /* Firefox 3.5+ */
    filter: gray; /* IE6-9 */
    -webkit-filter: grayscale(100%); /* Chrome 19+ & Safari 6+ */
    -webkit-transition: all .3s ease;
    -webkit-backface-visibility: hidden;
}

#skill-object-card:hover > #skill-object-card-image{
    filter: none;
    -webkit-filter: grayscale(0%);
}



