.ExperienceObject {
    width: 96%; 
    margin-right: 10px; margin-bottom: 10px; 
}

#experience-object-card {
    width: 100%; background-color: white;
    -o-transition: 0.3s;
    transition: 0.3s;
    -webkit-transition: 0.3s; -moz-transition: 0.3s;
}

#experience-object-card:hover {
    -webkit-box-shadow: 7px 7px 7px silver;
            box-shadow: 7px 7px 7px silver;
}

#experience-object-card-header {
    background-color: whitesmoke;
}

#experience-object-card-subheader {
    display: -ms-flexbox;
    display: flex; -ms-flex-direction: row; flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap; 
}

#experience-object-card-subheader-company-city {
    margin-left: 0px; margin-right: auto;
    font-weight: normal; width: -webkit-max-content; width: -moz-max-content; width: max-content;
}

#experience-object-card-subheader-duration {
    margin-left: auto; margin-right: 0px;
    font-weight: bolder; width: -webkit-max-content; width: -moz-max-content; width: max-content;
}

#experience-object-card-skills {
    background-color: whitesmoke;
}

#skills-chip {
    margin-right: 5px; margin-bottom: 5px;
}

#experience-object-actions {
    background-color: rgb(230,230,230);
}


